import React, { useEffect, useState } from 'react';
import api from '../../../services/api';
import './ResultForm.css';
import VoiceInput from '../../../shared/components/VoiceInput';

export default function ResultForm({ onSaved, onCancel, parentId }) {
  const [form, setForm] = useState({
    title: '',
    final_result: '',
    date: '',
    due_date: '',
    urgent: false,
    description: '',
    responsible_id: ''
  });
  const [users, setUsers] = useState([]);
  const [error, setError] = useState('');
  const [dueDateValid, setDueDateValid] = useState(true);
  const [dateValid, setDateValid] = useState(true);

  useEffect(() => {
    const loadUsers = async () => {
      try {
        const r = await api.get('/users', { params: { active: 1 } });
        setUsers(r.data || []);
      } catch {
        // ignore
      }
    };
    loadUsers();
  }, []);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    const val = type === 'checkbox' ? checked : value;
    setForm((prev) => ({ ...prev, [name]: val }));

    if (name === 'due_date') {
      // очікуємо формат гг:хх (24h), напр. 09:30
      const re = /^\d{2}:\d{2}$/;
      setDueDateValid(re.test(val) || val === '');
    }

    if (name === 'date') {
      // дата не може бути в минулому
      const selected = new Date(val);
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      setDateValid(selected >= today || val === '');
    }
  };
  const handleVoice = (data) => {
    setForm((prev) => ({
      ...prev,
      title: data.title || prev.title,
      final_result: data.final_result || prev.final_result,
      date: data.date || prev.date,
      due_date: data.due_date || prev.due_date,
      urgent: data.urgent !== undefined ? data.urgent : prev.urgent,
      description: data.description || prev.description,
      responsible_id: data.responsible_id ? String(data.responsible_id) : prev.responsible_id,
    }));
  };


  const todayStr = new Date().toISOString().split('T')[0];

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    try {
      if (form.date) {
        const selected = new Date(form.date);
        const today = new Date();
        today.setHours(0, 0, 0, 0);
        if (selected < today) {
          setDateValid(false);
          return;
        }
      }

      if (form.due_date && !/^\d{2}:\d{2}$/.test(form.due_date)) {
        setDueDateValid(false);
        return;
      }

      const payload = {
        title: form.title,
        final_result: form.final_result,
        date: form.date || null,
        due_date: form.due_date || null,
        urgent: !!form.urgent,
        description: form.description,
        responsible_id: form.responsible_id ? Number(form.responsible_id) : null
      };

      if (parentId) {
        await api.post(`/results/${parentId}/children`, payload);
      } else {
        await api.post('/results', payload);
      }

      onSaved && onSaved();

      setForm({
        title: '',
        final_result: '',
        date: '',
        due_date: '',
        urgent: false,
        description: '',
        responsible_id: ''
      });
      setDueDateValid(true);
      setDateValid(true);
    } catch (e) {
      const msg = e?.response?.data?.message || 'Не вдалося створити результат';
      setError(msg);
    }
  };

  const labelForUser = (u) => {
    if (u.first_name || u.last_name) {
      return `${u.first_name || ''} ${u.last_name || ''}`.trim();
    }
    return u.username || `ID ${u.id}`;
  };

  return (
    <div className="result-form card">
      <form onSubmit={handleSubmit}>
        <VoiceInput endpoint="/results/voice" onResult={handleVoice} />
        <label className="rf-field">
          <span>Назва*</span>
          <input
            type="text"
            name="title"
            className="input"
            value={form.title}
            placeholder="Введіть назву"
            onChange={handleChange}
            required
          />
        </label>

        <div className="rf-row">
          <label className="rf-field">
            <span>Кінцевий результат*</span>
            <input
              type="text"
              name="final_result"
              className="input"
              value={form.final_result}
              placeholder="Опишіть результат"
              onChange={handleChange}
              required
            />
          </label>

          <label className="rf-field">
            <span>Дата</span>
            <input
              type="date"
              name="date"
              className={`input${!dateValid && form.date ? ' invalid' : ''}`}
              value={form.date}
              min={todayStr}
              onChange={handleChange}
            />
            {!dateValid && form.date && (
              <span className="rf-hint">Дата не може бути в минулому</span>
            )}
          </label>

          <label className="rf-field rf-due">
            <span>Кінцевий термін</span>
            <input
              type="text"
              name="due_date"
              className={`input${!dueDateValid && form.due_date ? ' invalid' : ''}`}
              placeholder="гг:хх"
              value={form.due_date}
              onChange={handleChange}
            />
            {!dueDateValid && form.due_date && (
              <span className="rf-hint">Формат: гг:хх</span>
            )}
          </label>

          <label className="rf-check">
            <input
              type="checkbox"
              name="urgent"
              checked={form.urgent}
              onChange={handleChange}
            />
            <span>Терміново</span>
          </label>
        </div>

        <label className="rf-field">
          <span>Опис</span>
          <textarea
            name="description"
            className="input"
            value={form.description}
            placeholder="Додайте опис"
            onChange={handleChange}
          />
        </label>

        <label className="rf-field">
          <span>Відповідальний*</span>
          <select
            name="responsible_id"
            className="input"
            value={form.responsible_id}
            onChange={handleChange}
            required
          >
            <option value="">Оберіть відповідального</option>
            {users.map((u) => (
              <option key={u.id} value={u.id}>
                {labelForUser(u)}
              </option>
            ))}
          </select>
        </label>

        <div className="rf-error" aria-live="polite">{error}</div>

        <div className="rf-actions">
          <button type="submit" className="btn primary">Зберегти</button>
          <button type="button" className="btn ghost" onClick={onCancel}>Скасувати</button>
        </div>
      </form>
    </div>
  );
}
